<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-profile ref="formProfileRef" :checked="checked"></form-profile>
    <list-profile class="px-4" title="一般资料" ref="normalProfileRef"></list-profile>
    <list-profile class="px-4 mt-4" title="疗效观察指标" :showAddBtn="true" ref="medicalProfileRef" ></list-profile>
    <file-upload class="px-4 pt-5" title="上传参考文献" ref="profileUploadRef" desc="上传国标参考文献、用于对临床指标、治疗方法定义说明，1-4篇文献"></file-upload>
  </div>
</template>

<script>
import FormProfile from "./FormProfile.vue"
import ListProfile from "./ListProfile.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FormProfile,
    ListProfile,
    FileUpload,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formProfileRef.resetRequiredForm(value)
      this.$refs.normalProfileRef.resetRequiredValue(value)
      this.$refs.medicalProfileRef.resetRequiredValue(value)
      this.$refs.profileUploadRef.resetRequiredFile(value)
      this.$bus.emit('fromProfileCheckbox', value)
    },
  },
  created() {
    this.$nextTick(() => {
      this.$refs.normalProfileRef.list = [
        {
          id: "人口学资料",
          content: "年龄、性别、民族",
        },
        {
          id: "病史指标",
          content: "无吸烟史、饮酒史",
        },
        {
          id: "临床指标",
          content: "收缩压(SBP)、舒张压(DBP)",
        },
        {
          id: "其他疾病情况",
          content: "是否有高血压、高血脂、高血糖",
        },
      ]
      this.$refs.medicalProfileRef.list = [
        {
          id: "",
          content: "治疗是否有效",
        },
        {
          id: "",
          content: "24时是否发生术后并发症",
        },
        {
          id: "",
          content: "平均住院时间(天)",
        },
        {
          id: "",
          content: "平物恢复时间(天)",
        },
        {
          id: "",
          content: "治疗费用",
        },
        {
          id: "",
          content: "3个月内疾病是否复发",
        },
      ]
    })
  },
}
</script>
