    </el-checkbox>
    <!-- <div class="pl-4 py-3 flex items-center">
      <div class="leading-none mr-2"><span class="text-[#F56C6C] mr-1" v-show="checked">*</span>解读内容：</div>
      <el-radio-group v-model="radio">
        <el-radio :label="item" v-for="(item, i) of radioList" :key="i">{{ item }}</el-radio>
      </el-radio-group>
    </div> -->
    <file-upload class="px-4" title="基线表" :showDownloadBtn="true" ref="resultUploadRef"
