<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox" :disabled="!disabled">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <div class="text-center">
      <el-checkbox-group v-model="checkList" >
        <el-checkbox label="中文摘要" :disabled="!checked"></el-checkbox>
        <el-checkbox label="英文摘要" :disabled="!checked"></el-checkbox>
      </el-checkbox-group>
    </div>
  </div>
</template>

<script>
export default {
  computed:{
    disabled(){
      return this.discussionCheckbox && this.introductionCheckbox && this.profileCheckbox && this.resultCheckbox
    }
  },
  data() {
    return {
      checked: false,
      checkList: [],
      discussionCheckbox: false,
      introductionCheckbox: false,
      profileCheckbox: false,
      resultCheckbox: false,
    }
  },
  methods: {
    changeCheckbox(value) {

    },
  },
  created() {
    this.$bus.on("fromDiscussionCheckbox", data => {
      this.discussionCheckbox = data
    })
    this.$bus.on("fromIntroductionCheckbox", data => {
      this.introductionCheckbox = data
    })
    this.$bus.on("fromProfileCheckbox", data => {
      this.profileCheckbox = data
    })
    this.$bus.on("fromResultCheckbox", data => {
      this.resultCheckbox = data
    })
  }
}
</script>
