<template>
  <div>
    <el-checkbox v-model="checked" class="pl-4 pt-3 pb-2" @change="changeCheckbox">
      <span class="text-base">是否撰写本章节</span>
    </el-checkbox>
    <form-introduction ref="formIntroductionRef" :checked="checked"></form-introduction>
    <file-upload class="px-4" title="上传参考文献" ref="normalIntroductionRef" desc="上传：综述类文献、meta分析、医药说明、治疗指南、个案、标准等文献"></file-upload>
  </div>
</template>

<script>
import FormIntroduction from "./FormIntroduction.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FormIntroduction,
    FileUpload,
  },
  data() {
    return {
      checked: false,
    }
  },
  methods: {
    changeCheckbox(value) {
      this.$refs.formIntroductionRef.resetRequiredForm(value)
      this.$refs.normalIntroductionRef.resetRequiredFile(value)
      this.$bus.emit('fromIntroductionCheckbox', value)
    },
  },
}
</script>
